import React from 'react';
import { Link } from 'react-router-dom';
import './css/Navigation.css';

function Navigation() {
  return (
    <ul className="nav">
      <li>
        <Link to="/">Home</Link>
      </li>
      <li>
        <Link to="/about">About</Link>
      </li>
    </ul>
  );
}

export default Navigation;
